import appRoutes from "shared/constants/appRoutes";
import { getLastPartInTitleCase, pluralize } from "shared/helpers/string";
import timeIcon from "shared/images/calendar-time.svg";
import lockIcon from "shared/images/lock.svg";
import moneyIcon from "shared/images/money-bag.svg";
import convertIcon from "shared/images/convert.svg";

/**
 * The 'count' — second argument to pluralize(...) — in 'description' property
 * could be made dynamic based on length of 'toolsListMap' of each tools category.
 * However, in order to keep the chunk size of the main tools page (tools category
 * listing page) minimal, intentionally avoid importing files of tools into this file.
 * Hard-coding the 'count' value is the trade-off we are making in favor
 * of optimal chunk size.
 */
const subPagesMap = new Map([
  /**
   * Tools::Crypto
   */
  [
    appRoutes.toolsCrypto.path,
    {
      name: getLastPartInTitleCase(appRoutes.toolsCrypto.path, "/"),
      icon: lockIcon,
      description: pluralize(`${appRoutes.toolsCrypto.title} tool`, 1)
    }
  ],
  /**
   * Tools::Money
   */
  [
    appRoutes.toolsMoney.path,
    {
      name: getLastPartInTitleCase(appRoutes.toolsMoney.path, "/"),
      icon: moneyIcon,
      description: pluralize(`${appRoutes.toolsMoney.title} tool`, 2)
    }
  ],
  /**
   * Tools::Convert
   */
  [
    appRoutes.toolsConvert.path,
    {
      name: getLastPartInTitleCase(appRoutes.toolsConvert.path, "/"),
      icon: convertIcon,
      description: pluralize(`${appRoutes.toolsConvert.title} tool`, 2)
    }
  ],
  /**
   * Tools::Time
   */
  [
    appRoutes.toolsTime.path,
    {
      name: getLastPartInTitleCase(appRoutes.toolsTime.path, "/"),
      icon: timeIcon,
      description: pluralize(`${appRoutes.toolsTime.title} tool`, 2)
    }
  ]
]);

const toolsConstants = {
  subPagesMap
};

export default toolsConstants;
